//1.导入vue和vue-router模块
import Vue from "vue";
import VueRouter from 'vue-router'


import Subject from '../views/Subject.vue'

//2.使用路由模块  注册路由模块
Vue.use(VueRouter)
//3.配置路由规则
const routes = [
    { path: '/', redirect: 'login' },
    {
        path: '/login',
        //component: Login,
        component: () => import('../views/Login.vue'),
        meta: { title: '登录页面' },
        //配置独享路由守卫
    },
    {
        path: '/index',
        //component: Index,
        component: () => import('../views/Index.vue'),
        meta: { title: '首页' },
        children: [
            { path: '/', redirect: 'stu' },
            {
                path: 'stu',
                //component: Student,
                component: () => import('../views/Student.vue'),
                meta: { title: '学生信息' },
                children: [
                    //{ path: '/', redirect: 'class' },
                    {
                        path: 'class',
                        //component: Class,
                        component: () => import('../components/Class.vue'),
                        meta: { title: '班级信息' },
                    },
                    {
                        path: 'address',
                        //component: Address,
                        component: () => import('../components/Address.vue'),
                        meta: { title: '地址信息' },
                    }
                ]
            },
            {
                path: 'sub',
                component: Subject,
                meta: { title: '科目' },
                children: [
                    {
                        path: 'subInfo/:id',
                        component: () => import('../views/Subject.vue'),
                        props: true,
                        meta: { title: '科目详情信息' },
                    }
                ]
            }
        ]
    },
]


const router = new VueRouter({
    mode: 'history',
    routes
});
//添加路由守卫
router.beforeEach(function (to, from, next) {
    //console.log(to);
    let token = localStorage.getItem('token');
    //console.log(token);
    if (to.path === '/login') {
        //当访问的页面是login时  判断此时是否有了token
        //如果有token证明 已经登录过了
        if (token) {
            //直接强制跳转到index首页去
            next('/index')
        } else {
            //否则 就放行 能让客户访问login页面
            next()
        }
    } else if (to.path.includes('/index')) {
        if (token) {
            next();
        } else {
            next('/login')
        }
    } else {
        next('/login')
    }
})
//定义全局后置路由守卫
router.afterEach(function (to, from) {
    console.log('后置守卫执行了')
    console.log(to.meta.title);
    if (to.meta.title) {
        document.title = to.meta.title
    }
})
